import * as React from 'react';
import { Controller } from '@ali/4ever-cangjie';
import { PluginRoles } from '@ali/4ever-bamboo';
import { IBasicButton, IToolbarButtonConfig, IconFont, IconReduceFont } from '@ali/4ever-component';
import { adjustSz } from '../../actions';
import { SHORTCUT } from '../../utils/getTipsByType';

export interface SzButtonProps extends IToolbarButtonConfig {
  controller: Controller;
  locale?: Record<string, string>;
}


export function createSzSmallerButton(props: SzButtonProps): IBasicButton {
  const { controller, locale, icon, title, tooltip, badgeDot, disabled } = props;

  const handleClick = () => {
    controller.run('onAction', adjustSz(false));
  };

  const isDisabled = disabled || controller.query('isSelectionInLinkCard');

  const buttonConfig: IBasicButton = ({
    icon: icon || <IconReduceFont />,
    onClick: handleClick,
    title: title || locale?.buttonTitle,
    tooltip: {
      title: title || locale?.buttonTitle,
      shortCut: SHORTCUT.smaller,
      ...tooltip,
    },
    badgeDot,
    disabled: !!isDisabled,
    testid: "ReduceFontSizeButton",
    role: PluginRoles.szDecrease
  });

  return buttonConfig;

}


export function createSzBiggerButton(props: SzButtonProps): IBasicButton {
  const { controller, locale, icon, title, tooltip, badgeDot, disabled } = props;

  const handleClick = () => {
    controller.run('onAction', adjustSz(true));
  };

  const isDisabled = disabled || controller.query('isSelectionInLinkCard');

  const buttonConfig: IBasicButton = ({
    icon: icon || <IconFont />,
    onClick: handleClick,
    title: title || locale?.buttonTitle,
    tooltip: {
      title: title || locale?.buttonTitle,
      shortCut: SHORTCUT.bigger,
      ...tooltip,
    },
    badgeDot,
    disabled: !!isDisabled,
    testid: "add_fontsize_button",
    role: PluginRoles.szIncrease
  });

  return buttonConfig;

}


